.story {
    display: flex;
    flex-direction: column;
    min-width: 7rem;
    max-width: 7rem;
    overflow: hidden;
    position: relative;
}

.image {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 0.5rem;
    box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    position: absolute;
}

.name {
    position: absolute;
    bottom: 0.5rem;
    left: 0.5rem;
    color: white;
    font-size: 0.8rem;
}

.skeletonContainer {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 0.5rem;
    box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    position: absolute;
}